
const List = (props) => {
    return (
        <div className="list-item">
            <li id={props.id}>
                {props.value}
            </li>
            <span
                className="delete-list"
                onClick={() => {
                    props.onDelete(props.id);
                }}
            >&times;</span>
        </div>
    )
}

export default List;
