import { useState } from 'react';
import List from './components/List';
import './index.css';


const App = () => {

    const [inputText, setInputText] = useState("");

    // const existingList = localStorage.getItem("updatedList");

    // console.log(existingList);
    
    // init. an empty array for storing subsequent lists to be added
    const [todoList, setTodoList] = useState([]); 
    
    

    // update the input value by setting it to the value of the input
    function grabInput(e) {
        const value = e.target.value;

        setInputText(value);
        
    }

    // grab the input list and add it to the previous array of lists
    function updateList() {
        setTodoList(prevList => {
            // console.log(prevList);
            if (inputText === "") {
                return [...prevList];
             } else {
                return [...prevList, inputText];
             }
        })

        // localStorage.setItem("updatedList", todoList);
        // reset input field
        setInputText("");
        // set local storage
        ;
    }


    function deleteItem(id) {
        // localStorage.getItem("updatedList");

        setTodoList(prevList => {
             return prevList.filter((prevItem, index) => {
                return index !== id;
            });
        });
    
    }


    return (
        <div className="wrapper">
            <h1>To-Do List</h1>

            <div className="form-group">
                <input 
                    type="text" 
                    value={inputText} 
                    placeholder="add item"
                    onChange={grabInput}/>
                <button 
                    type="submit"
                    onClick={updateList}
                >Add</button>
            </div>

            <ul className="todo">
                {todoList.map((list, index) => 
                    <List 
                        key={index}
                        id={index}
                        value={list}
                        onDelete={deleteItem}
                    />
                )}
            </ul>
        </div>
    )
}

export default App;